require.config({
	'baseUrl': 'scripts/',

	'paths': {
		'jquery': 'lib/jquery.min',
		'underscore': 'lib/lodash.min',
		'backbone': 'lib/backbone-min',

		'augmented': 'core/augmented',
		'augmentedPresentation': 'presentation/augmentedPresentation',
		'augmentedService': 'service/augmentedService',
		'augmentedLegacy': 'legacy/legacy',

		'jasmine': 'lib/jasmine-2.x/jasmine',
		'jasmine_html': 'lib/jasmine-2.x/jasmine-html',
		'boot': 'lib/jasmine-2.x/boot',
        'jasmineajax': 'lib/mock-ajax'

	},
	'shim': {
		jasmine: {
			exports: 'window.jasmineRequire'
		},
		jasmine_html: {
			deps: [ 'jasmine' ],
			exports: 'window.jasmineRequire'
		},
		boot: {
			deps: [ 'jasmine', 'jasmine_html' ],
			exports: 'window.jasmineRequire'
		},
        jasmineajax: {
            deps: [ 'jasmine' ],
            exports: 'jasmine-ajax'
        }
	}
});

//Define all of your specs here. These are RequireJS modules.
var specs = [ 'core/test/coreSpec',
              'core/test/validationSpec',
              'core/test/localStorageSpec',
              'core/test/utilitySpec',
              'core/test/resourceBundleSpec',
              'core/test/ajaxSpec',
              'core/test/applicationSpec',
			  'core/test/securitySpec',
			  'core/test/loggerSpec',
			  'core/test/asyncQueueSpec',
              'core/test/modelSpec',
              'core/test/collectionSpec',
              'core/test/viewSpec',
              'core/test/transformerSpec',

              'presentation/test/presentationSpec',
              'presentation/test/autoTableSpec',
              'presentation/test/applicationSpec',
              'presentation/test/mediationSpec',
              'presentation/test/decoratorViewSpec',

              //TODO: redo these to work with the new system
              //'service/test/mockServiceSpec',

			  'legacy/test/applicationContextSpec',
              'legacy/test/polyfillSpec'
            ];

// Load Jasmine - This will still create all of the normal Jasmine browser globals unless `boot.js` is re-written to use the
// AMD or UMD specs. `boot.js` will do a bunch of configuration and attach it's initializers to `window.onload()`. Because
// we are using RequireJS `window.onload()` has already been triggered so we have to manually call it again. This will
// initialize the HTML Reporter and execute the environment.
require(['augmented', 'augmentedPresentation', 'boot'], function(Augmented, Presentation) {
    "use strict";
    var app = new Augmented.Presentation.Application("Jasmine Suite");
    //app.registerStylesheet("styles/main.css");
    app.registerStylesheet("https://fonts.googleapis.com/css?family=Roboto:100,300,400");
    app.start();

    Augmented.Presentation.Dom.setValue("h2#augmented", "<span class=\"version\">Version " + Augmented.VERSION + " (" + Augmented.codename + ")</span>&emsp;<span class=\"release\">Release (" + Augmented.releasename + ")</span>");

	// Load the specs
	require(specs, function() {
		// Initialize the HTML Reporter and execute the environment (setup by `boot.js`)
		window.onload();
	});
});

define("augmentedJasmineRequire", function(){});

